  .social-block__list li {
    margin: 0 1.25rem;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .social-block__list li {
    margin: 0 1.25rem;
  }
}
.social-block__list li:nth-child(2) {
  transition-delay: 100ms;
}
.social-block__list li:nth-child(3) {
  transition-delay: 200ms;
}

.social-block__link {
  position: relative;
  display: block;
  width: 4rem;
  height: 4rem;
  color: var(--c-light);
  border: 2px solid transparent;
  border-radius: 50%;
}
@media (min-width: 2000px) {
  .social-block__link {
    width: 40px;
    height: 40px;
  }
}
@media (max-width: 1024px) and (orientation: portrait) {
  .social-block__link {
    color: var(--c-light);
    background-color: var(--c-dark);
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .social-block__link {
    color: var(--c-light);
    background-color: var(--c-dark);
  }
}
.social-block__link:hover {
  color: var(--c-puple);
}
.social-block__link:focus {
  color: var(--c-puple);
  background-color: var(--c-light);
  border-color: var(--c-puple);
  outline: none;
}
.social-block__link:active, .social-block__link:active:focus {
  color: var(--c-puple);
  background-color: var(--c-light);
  border-color: var(--c-light);
}
.social-block__link svg {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.social-block__link--fb svg {
  width: 0.8rem;
  height: 1.7rem;
}
@media (min-width: 2000px) {
  .social-block__link--fb svg {
    width: 8px;
    height: 17px;
  }
}
.social-block__link--insta svg {
  width: 1.5rem;
  height: 1.5rem;
}
@media (min-width: 2000px) {
  .social-block__link--insta svg {
    width: 15px;
    height: 15px;
  }
}
.social-block__link--vk svg {
  width: 1.7rem;
  height: 1rem;
}
@media (min-width: 2000px) {
  .social-block__link--vk svg {
    width: 10px;
    height: 10px;
  }
}

.accent-text {
  display: flex;
  flex-wrap: wrap;
}

.accent-text__letter-container {
  overflow: hidden;
  padding: 2.5rem 0.5rem 0 0;
  margin: -2.5rem -0.5rem 0 0;
}

.accent-text__letter {
  transform: translate3d(0, 1em, 0);
  animation-name: slideUp;
  animation-timing-function: ease-out;
  animation-duration: 0.3s;
  animation-fill-mode: forwards;
}